import React from 'react';
import styled from 'styled-components';

const ProductItemBlock = styled.div`
    width:50%;
    display:flex; 
    margin-bottom:30px; align-items:center;
    img { width:130px }
    div { 
        padding:0 10px; 
        p:nth-child(1) { font-size:20px }
        p:nth-child(2) { font-size:18px; margin:10px 0 }
    }
`;


const ProductItem = ({name, depart, about, photo}) => {
    // const docName = name;
    // const docAbout = about;
    return (
        <ProductItemBlock>
            <img src={photo} alt={about} />
            <div>
                <p dangerouslySetInnerHTML={{ __html:name }} />
                <p>{depart}</p>
                <p dangerouslySetInnerHTML={{ __html:about }} />
            </div>
        </ProductItemBlock>
    );
};

export default ProductItem;