import React, {useState, useCallback, useEffect} from 'react';
import styled, {css} from 'styled-components';
import axios from 'axios';
import NewsItem from './NewsItem';
import { AiOutlineLoading3Quarters } from "react-icons/ai";


const NewsBlock = styled.div`
    border-top:103.2px solid blue;
    .loadWrap { 
        display:flex; justify-content:center; align-items:center; min-height:80vh;
        .loadIcon {
            font-size:100px; color:#000;
            animation: loadSpin infinite 5s linear;
        }
        @keyframes loadSpin {
            from { transform: rotate(0deg)}
            to { transform: rotate(3turn) }
        }
    }
    .row {
        .category {
            display:flex; justify-content:center;
        }
        .articles {
            display:flex; flex-wrap:wrap;
        }
    }
`;

const Cate = styled.div`
    padding:10px 20px; background:#ddd; border-radius:10px; 
    cursor:pointer; margin:50px 10px; 
    ${
        props => props.active && css`background:#333; color:#fff`
    }
`;

const categories = [
    { name:'all', text:'전체보기' },
    { name:'business', text:'비즈니스' },
    { name:'entertainment', text:'엔터테인먼트' },
    { name:'health', text:'건강' },
    { name:'science', text:'과학' },
    { name:'sports', text:'스포츠' },
    { name:'technology', text:'기술' },
]

const Newssection = () => {

    const [category, setCategory] = useState('all')
    const onChange = useCallback(
        (name) => {
            setCategory(name)
        }
        ,[])

    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);
    
    const api = axios.create({
        baseURL: "http://cors-anywhere.herokuapp.com/http://apis.data.go.kr/"
      });

    const getNews = async () => {
        const query = category==='all' ? '' : `&category=${category}`;
        const url = `https://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=11ba08dfbd7e4f3c83ac93b27aa1dcb7`;
        const response = await api.get(url)
        console.log(response)
        setArticles(response.data.articles)
        setLoading(true);
    }    

    useEffect(
        () => {
            getNews()
        }
        , [category]) 

    if (!loading) {
        return (
            <NewsBlock>
                <div className="loadWrap">
                    <AiOutlineLoading3Quarters className="loadIcon" />
                </div>
            </NewsBlock>
        );
    } else {
        return (
            <NewsBlock>
                <div className="row">
                    <div className="category">
                        {
                            categories.map( (data, index) => <Cate active={category === data.name} key={index} onClick={()=>onChange(data.name)}>{data.text}</Cate> )
                        }
                    </div>
                    <div className="articles">
                        {
                          articles &&  articles.map( (article, index) => <NewsItem key={index} article={article} /> )
                        }
                    </div>
                </div>
            </NewsBlock>
        );
    }

    
};

export default Newssection;