import React, {useEffect} from 'react';
import styled from "styled-components";
/*global kakao*/

const Maps = styled.div`
      height: 600px;
      margin-top:100px;
      margin-bottom:100px;
`;

const Map = () => {

  useEffect( () => { 
        const script = document.createElement("script");
        script.async = true;
        script.src ="https://dapi.kakao.com/v2/maps/sdk.js?appkey=6ef95ecd5b4f155dacee5677b0db88cf&libraries=LIBRARY";
        document.head.appendChild(script);

        script.onload = () => {
            kakao.maps.load(() => {
              let container = document.getElementById("map");
              let options = {
                center: new kakao.maps.LatLng(37.506502, 127.053617), // 지도의 중심좌표
                level: 7    // 지도의 확대레벨(1~7)
              };
              const map = new window.kakao.maps.Map(container, options); // 지도생성
            });
        };
       }, [])

  return (
      <Maps id="map" className="row"></Maps>
  );
};

export default Map;


