import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const NewsItemBlock = styled.div`
    width:50%; display:flex; margin-bottom:20px; 
    .thumbnail { width:30%; padding-right:20px;  }
    .contents { width:70%; padding-right:20px;
        h2 { color:orange }
    }
`;

const NewsItem = ({article}) => {
    const { title, description, urlToImage } = article;
    return (
        <NewsItemBlock>
            {
                urlToImage && (
                    <div className="thumbnail">
                        <Link to={{ pathname:'/news/detail', state:{urlToImage, title, description}  }}><img src={urlToImage} alt={title} /></Link>
                    </div>
                )
            }
            {
                description && (
                    <div className="contents">
                        <h2>{title}</h2>
                        <p>{description.slice(0,30)}...</p>
                    </div>
                )
            }
        </NewsItemBlock>
    );
};

export default NewsItem;